.list-user {
  padding-left: 0;
  margin: 0;
}

li {
  list-style: none;
  padding-top: 5px;
}

.elem-user {
  display: flex;
  align-items: center;
  width: 260px;
  padding-left: 15px;
}

.bar {
  width: 210px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.avatar {
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.add-user {
  cursor: pointer;
  width: 16px;
  height: 16px;
}

.info-user {
  text-align: left;
  padding-left: 5px;
}

.name {
  margin: 0;
  font-size: 12px;
}

.link-mail {
  font-size: 12px;
  color: #000;
  text-decoration: none;
}
