.search {
  display: inline-flex;
  border: 1px solid black;
  border-radius: 30px;
  padding: 10px;
  margin: 20px 0 15px;
}

.search-icon {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.search-field {
  width: 150px;
  border: none;
}

.search-field:focus {
  outline: none;
}
